import html2canvas from 'html2canvas';
import PropTypes from 'prop-types';
import React, { useCallback, useState, useMemo, useEffect } from 'react';
import { debounce } from 'lodash';
import { FiImage } from 'react-icons/fi';

import { cls } from '@mssgme/helpers';
import { LoadingIndicator, LandingsPropTypes } from '@mssgme/ui';
import { Page } from '@mssgme/userpage-core';

import styles from './PageThumbnailRenderer.scss';

const CanvasRenderer = ({ website, page, width, height, delay, onRendered, onError }) => {
    const style = useMemo(
        () => ({ width, minWidth: width, height, minHeight: height, boxShadow: 'none', borderRadius: 0 }),
        [width, height]
    );
    const [elemRef, setRef] = useState();
    const render = useCallback(async (element) => {
        try {
            const canvas = await html2canvas(element, {
                width,
                height,
                useCORS: true,
            });

            const blob = await new Promise((resolve, reject) => {
                try {
                    canvas.toBlob(resolve, 'image/jpeg', 80);
                } catch (e) {
                    reject(e);
                }
            });

            if (!blob) {
                return false;
            }

            return blob;
        } catch (e) {
            // eslint-disable-next-line no-console
            console.log(e);
            return false;
        }
    });

    const ref = useCallback(
        (element) => {
            if (!element) {
                return;
            }

            onRendered(null);
            setRef(element);
        },
        [page, website, onRendered]
    );

    useEffect(() => {
        if (!elemRef) {
            return;
        }

        const runner = debounce(async (node, attempts = 2) => {
            const data = await render(node);
            runner.cancel();

            if (data) {
                onRendered(data);
            } else if (--attempts > 0) {
                runner(node, attempts);
            } else {
                onError && onError(new Error('Can\'t generate preview'));
            }
        }, delay);

        onError && onError(null);
        runner(elemRef);

        return runner.cancel;
    }, [elemRef, onRendered]);

    return (
        <div className={styles.sample} style={style}>
            <Page ref={ref} style={style} interactive={false} website={website} page={page} />
        </div>
    );
};

CanvasRenderer.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired,
    delay: PropTypes.number.isRequired,
    onRendered: PropTypes.func.isRequired,
    onError: PropTypes.func,
};

export const PageThumbnailRenderer = ({
    website,
    page,
    width,
    height,
    delay,
    src,
    error,
    onChange,
    onError,
    ...rest
}) => {
    return (
        <div {...cls(styles.root, rest)}>
            <div className={styles.insert}>
                {src ? (
                    <img src={src} alt="Thumbnail" />
                ) : error ? (
                    <div
                        style={{
                            position: 'absolute',
                            width: '100%',
                            height: '100%',
                            alignItems: 'center',
                            justifyContent: 'center',
                            display: 'flex',
                        }}
                    >
                        <FiImage size={20} color="#999" />
                    </div>
                ) : (
                    <LoadingIndicator />
                )}
            </div>

            <div className={styles.offscreen}>
                <CanvasRenderer
                    website={website}
                    page={page}
                    width={width}
                    height={height}
                    delay={delay}
                    onRendered={onChange}
                    onError={onError}
                />
            </div>
        </div>
    );
};

PageThumbnailRenderer.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
    src: PropTypes.string,
    error: PropTypes.string,
    width: PropTypes.number,
    height: PropTypes.number,
    delay: PropTypes.number,
    onChange: PropTypes.func.isRequired,
    onError: PropTypes.func,
};

PageThumbnailRenderer.defaultProps = {
    width: 375,
    height: 506,
    delay: 1500,
};

export default PageThumbnailRenderer;
